.top-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;

  // padding: 20px 20px 0;

  // @include onTablet {
  //   padding: 20px 40px 0;
  // }
  // @include onLaptop {
  //   padding: 50px 55px 0;
  // }
  // @include onDesktop {
  //   padding: 50px 220px 0;
  // }

  &__wrapper {
    display: flex;
    align-items: center;
    column-gap: 36px;
  }

  &__link {
    // @include onTablet {
    //   margin-left: 110px;
    // }
    // @include onDesktop {
    //   margin-left: 230px;
    // }

    font-weight: 700;
    font-size: 13px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;

    color: $col_white;
    text-decoration: none;

    @include hover (color, $col_coral);

    &::after {
      content: "";
      display: block;
      height: 2px;
      background-color: $col_coral;
    }
  }
}
